.rightStickyButtons {
  position: fixed;
  right: em(73);
  bottom: em(50);
  z-index: 5;
  ul {
    margin: 0;
    padding: 0;
    li {
      list-style: none;
      display: block;
      width: em(42);
      height: em(42);
      + li {
        margin: em(10) 0 0 0;
      }
      a {
        width: 100%;
        height: 100%;
        @include transitions(0.5s);
        display: flex;
        justify-content: center;
        align-items: center;
        @include borderRadius(16px);
        &.call {
          background: var(--gold);
          i {
            width: em(12);
            height: em(12);
          }
        }
        &.whatsApp {
          background: #60D669;
        }
        i {
          width: em(20);
          height: em(20);
        }
        &:hover {
          text-decoration: none;
        }
      }
    }
  }
  @media #{$ipadpro} {
    right: em(30);
    bottom: em(30);
  }
}