.aboutSection {
  position: relative;
  overflow: hidden;
  background: var(--navyBlue);
}

.aboutBg {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  width: 100%;
  min-height: em(1080);
  @media #{$desktop} {
    clip-path: circle(10% at center);
    @include transitions(1.5s);
    &.animateIn{
      clip-path: circle(100% at center);
    }
  }
  @media #{$mobL} {
    min-height: em(600);
  }
  @supports (-webkit-touch-callout: none) {
    background-attachment: initial !important;
  }
}

.aboutWrapper {
  position: relative;
  z-index: 3;
  background: var(--offWhite);
  .container-fluid,
  .container-s {
    position: relative;
    z-index: 2;
  }
  .videoBg {
    opacity: 0.02;
  }
  padding: 0 0 em(192) 0;
  h3, h6 {
    color: var(--navyBlue);
  }
  h6 {
    font-family: $arboriaBook;
    letter-spacing: 3px;
    font-weight: em(18);
    margin-bottom: em(20);
  }

  @media #{$ipadL}{
    padding: 0 0 em(80) 0;
  }
  @media #{$ipad}{
    padding: 0 0 em(50) 0;
  }
  @media #{$mobL} {
    padding: 0 0 em(50) 0;
  }
}

.aboutCircle {
  width: 100%;
  height: em(315);
  overflow: hidden;
  position: absolute;
  left: 0;
  top: em(-315);
  &:before {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, 0);
    background: var(--offWhite);
    width: em(3246);
    height: em(3246);
    @include borderRadius(50%);
    content: '';
  }
  @media #{$ipadL} and #{$ipadpro} {
    height: em(200);
    top: em(-200);
    &:before {
      width: em(2800);
      height: em(2800);
    }
  }
  @media #{$mobL} {
    height: em(150);
    top: em(-150);
    &:before {
      width: em(2800);
      height: em(2800);
    }
  }
}

.aboutHeading {
  margin-bottom: em(128);
  @media #{$ipadL}{
    margin-bottom: em(60);
  }
  @media #{$ipad}{
    margin-bottom: em(60);
  }
  @media #{$mobL}{
    margin-bottom: em(60);
  }
}

.aboutImg {
  position: relative;
  width: 100%;
}

.aboutImg-1 {
  width: 100%;
  height: em(831);
  @media #{$desktop} {
    clip-path: inset(100% 0 0 0);
    @include transitions(1.5s);
    &.animated{
      clip-path: inset(0 0 0 0);
    }
  }
  @media #{$ipadL} and #{$ipadpro} {
    width: 80%;
    height: em(400);
  }
  @media #{$ipad}{
    width: 80%;
    height: em(400);
  }
  @media #{$mobL}{
    height: em(400);
  }
}

.aboutImg-2 {
  position: absolute;
  right: em(-156);
  bottom: em(-66);
  width: em(432);
  height: em(259);
  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @media #{$ipadL} and #{$ipadpro} {
    width: em(300);
    height: em(159);
    right: 0;
  }
  @media #{$ipad} {
    width: em(300);
    height: em(159);
    right: 0;
  }
  @media #{$mobL}{
    right: 1px;
    width: em(300);
    height: em(159);
  }
}

.aboutWrap {
  padding: em(72) 20% 0 em(100);
  @media #{$ipadL} and #{$ipadpro} {
    padding: em(100) 0 0 0;
  }
  @media #{$ipad} {
    padding: em(100) 0 0 0;
  }
  @media #{$mobL} {
    padding: em(90) 0 0 0;
  }
}

.aboutContent {
  color: var(--navyBlueDark);
  margin-bottom: em(113);
  @media #{$ipadL} and #{$ipadpro} {
    margin-bottom: em(80);
  }
  @media #{$ipad} {
    margin-bottom: em(80);
  }
  @media #{$mobL} {
    margin-bottom: em(80);
  }
}

.aboutNumbers {
  display: flex;
  flex-wrap: wrap;
}

.numberItem {
  width: 48%;
  padding: em(30);
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  p {
    color: var(--black);
  }
  &:before {
    position: absolute;
    right: 0;
    top: 0;
    width: 1px;
    height: 90%;
    background: var(--gold);
    content: '';
  }
  &:after {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 90%;
    height: 1px;
    background: var(--gold);
    content: '';
  }
  &:first-child {
    padding: em(30) em(30) em(30) 0;
  }
  &:nth-child(2) {
    text-align: end;
    &:before {
      display: none;
    }
  }
  &:nth-child(2) {
    &:before {
      display: none;
    }
    &:after {
      left: initial;
      right: 0;
    }
  }
  &:nth-child(3) {
    padding: em(50) em(30) em(30) 0;
    &:before {
      top: em(30);
      height: calc(90% - em(30));
    }
    &:after {
      display: none;
    }
  }
  &:last-child {
    text-align: end;
    padding: em(50) em(30) em(30) em(30);
    &:after, &:before {
      display: none;
    }
  }
  @media #{$mobL} {
    padding: em(30) em(20);
    &:first-child {
      padding: em(30) em(20) em(30) 0;
    }
    &:nth-child(3) {
      padding: em(30) em(20) em(30) 0;
    }
    &:last-child {
      text-align: end;
      padding: em(30) em(20);
    }
  }
}

.abNum {
  font-family: $basefont;
  font-size: em(76);
  letter-spacing: -8px;
  line-height: 1;
  color: var(--navyBlue);
  @media #{$mobL} {
    font-size: em(50);
    letter-spacing: -5px;
  }
}