footer {
  background: var(--navyBlue);
  padding: em(57) 0 0 0;
  position: relative;
  &:before {
    opacity: 0.5;
    background: linear-gradient(34deg, rgba(3, 7, 15, 0.00) 14.62%, #03070F 155.81%);
    mix-blend-mode: multiply;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    content: '';
  }
  .container-s {
    position: relative;
    z-index: 2;
  }
  @media #{$ipadL} and #{$ipadpro} {
    padding: em(50) 0 0 0;
  }
  @media #{$ipad}{
    padding: em(50) 0 0 0;
  }
  @media #{$mobL} {
    padding: em(50) 0 0 0;
  }
}

.ftLogoWrap {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  &:before,
  &:after {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 40%;
    height: 1px;
    background: var(--borderColor);
    content: '';
  }
  &:before {
    left: 0;
  }
  &:after {
    right: 0;
  }
  @media #{$ipadL} and #{$ipadpro} {
    &:before,
    &:after {
      width: 35%;
    }
  }
  @media #{$ipad} {
    &:before,
    &:after {
      width: 35%;
    }
  }
  @media #{$mobL} {
    &:before,
    &:after {
      width: 18%;
    }
  }
}

.ftLogo {
  width: em(260);
  height: em(71);
  text-decoration: none;
  display: block;
  @media #{$ipad} {
    width: em(180);
    height: em(50);
  }
}

.ftSpace {
  padding: em(81) 0;
  @media #{$ipad} {
    padding: em(40) 0;
  }
}

.ftAbout {
  width: em(420);
  @media #{$ipad} {
    width: 100%;
    margin-bottom: em(50);
    h6 {
      margin-bottom: em(20);
    }
    p {
      margin-bottom: em(40);
    }
  }
}

.ftNewsletter {
  position: relative;
  .submitBtn {
    position: absolute;
    right: 0;
    top: 0;
  }
  .form-control{
   min-height: em(60);
  }
}

.ftLinks {
  ul {
    margin: 0;
    padding: 0;
    li {
      list-style: none;
      margin: 0 0 em(35) 0;
      display: block;
      &:last-child {
        margin: 0;
      }
      a {
        color: var(--white);
        font-size: em(16);
        letter-spacing: 1px;
        line-height: 1;
        @include transitions(0.5s);
        display: inline;
        padding-bottom: 5px;
        background: linear-gradient(to right, var(--gold) 0%, var(--gold) 98%);
        background-size: 0px 1px;
        background-repeat: no-repeat;
        background-position: left 85%;
      }
    }
  }
  &.mb-space {
    @media #{$mobL}{
      margin-bottom: em(50);
    }
  }
}

.ftBtm {
  border-top: solid 1px var(--borderColor);
  padding: em(20) 0 em(30) 0;
  @media #{$ipad} {
    .col_md_12 {
      text-align: center;
      order: 3;
    }
    .copyTxt {
      margin-top: em(20);
    }
  }
}

.ftSocialLinks {
  ul {
    margin: 0;
    padding: 0;
    display: flex;
    li {
      list-style: none;
      width: em(42);
      height: em(42);
      margin: 0 em(10);
      &:first-child {
        margin: 0 em(10) 0 0;
      }
      &:last-child {
        margin: 0 0 0 em(10);
      }
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        position: relative;
        @include borderRadius(50%);
        overflow: hidden;
        &:before {
          background: var(--white);
          opacity: 0.2;
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          @include transitions(0.5s);
          content: '';
        }
        i {
          display: block;
          width: em(15);
          height: em(15);
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }

    @media #{$ipadL} and #{$ipadpro} {
      justify-content: center;
    }
    @media #{$mobL} {
      justify-content: center;
      margin-bottom: em(20);
    }
  }
}

.privacyLinks {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-end;
  @media #{$mobL} {
    justify-content: center;
  }
  ul {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: flex-end;
    li {
      list-style: none;
      margin: 0 em(47) 0 0;
      &:last-child {
        margin: 0 0 0 0;
      }
      a {
        color: var(--white);
        font-size: em(16);
        letter-spacing: 1px;
        line-height: 1;
        @include transitions(0.5s);
        display: inline;
        padding-bottom: 5px;
        background: linear-gradient(to right, var(--gold) 0%, var(--gold) 98%);
        background-size: 0px 1px;
        background-repeat: no-repeat;
        background-position: left 85%;

        &:hover {
          text-decoration: none;
        }
      }
    }
  }
}

@media (hover: hover) {
  .ftLinks, .privacyLinks {
    ul {
      li {
        a {
          &:hover {
            background-size: 100% 1px;
            text-decoration: none;
            color: var(--gold);
          }
        }
      }
    }
  }
  .ftSocialLinks {
    ul {
      li {
        a {
          &:hover {
            &:before {
              background: var(--gold);
            }
          }
        }
      }
    }
  }
}