.ourCoreValues {
  background: var(--offWhite);
}

.coreHeading {
  border-top: solid 1px var(--gold);
  padding: em(100) 0 em(60) 0;
  h3 {
    color: var(--navyBlue);
  }
  @media #{$ipadL} and #{$ipadpro} {
    padding: em(80) 0 em(60) 0;
  }
  @media #{$ipad}{
    padding: em(50) 0;
  }
  @media #{$mobL} {
    padding: em(50) 0;
  }
}

.itemWrapper {
  width: 100%;
  height: em(811);
  display: flex;
  position: relative;
  @media #{$ipadL} and #{$ipadpro} {
    height: em(600);
  }
  @media #{$ipad} {
    flex-wrap: wrap;
    height: initial;
  }
  @media #{$mobL} {
    display: block;
    height: initial;
  }
}

.cardItem {
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  padding: 0 em(30);
  position: relative;
  z-index: 3;
  overflow: hidden;
  .itemData {
    margin-top: em(30);
    height: 0%;
    overflow: hidden;
    @include transitions(0.5s);
    position: relative;
    z-index: 2;
    @media #{$mobL} {
      height: initial !important;
    }
  }
  h5 {
    color: var(--offWhite);
    position: relative;
    z-index: 2;
  }
  p {
    font-family: $secondaryFont;
    letter-spacing: initial;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 8;
    -webkit-box-orient: vertical;
    &.lead {
      margin-bottom: em(15);
    }
  }
  &:after {
    position: absolute;
    right: 0;
    top: 0;
    width: 1px;
    height: 100%;
    background: var(--white);
    opacity: 0.4;
    content: '';
  }
  &:before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(40px);
    background-color: rgba(var(--navyBlueDark), 0.2);
    opacity: 0;
    @include transitions(0.5s);
    content: '';
  }
  &:last-child {
    &:after{
      display: none;
    }
  }
  &.active {
    &:before {
      opacity: 0.98;
    }
    .itemData {
      height: 50%;
    }
  }
  @media #{$ipadL} and #{$ipadpro} {
    padding: 0 em(20);
    p {
      -webkit-line-clamp: 5;
    }
  }
  @media #{$ipad} {
    width: 50%;
    height: em(500);
    border-bottom: solid 1px rgba(#FFFFFF, 0.4);
    p{
      -webkit-line-clamp: 4;
    }

    &:nth-child(3){
      &:after{
        display: none;
      }
    }
    &:nth-child(4),
    &:last-child{
      border-bottom: none;
    }
  }
  @media #{$mobL} {
    width: 100%;
    padding: em(30);
    height: em(400);
    border-bottom: solid 1px rgba(#FFFFFF, 0.4);
    &:after{
      display: none;
    }
    &:before{
      opacity: 0.6;
      z-index: 1;
      backdrop-filter: blur(0px);
      background-color: var(--navyBlueDark);
    }
    &:last-child{
      border-bottom: none;
    }
  }
}

.itemBg {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  @include transitions(0.5s);
  &:before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.20) 100%);
    opacity: 0.2;
    content: '';
  }
  @media #{$mobL} {
    display: none;
  }
}

.cardBgMobile{
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: none;
  @media #{$mobL} {
    display: block;
  }
}