.featuredSection {
  padding: em(136) 0;
  background: var(--navyBlue);
  background: linear-gradient(180deg, #00133F 0%, #000218 100%);
  overflow: hidden;
  min-height: 100vh;
  position: relative;
  h6 {
    color: var(--gold);
  }
  @media #{$ipadL} and #{$ipadpro} {
    padding: em(80) 0;
    min-height: initial;
  }
  @media #{$ipad}{
    padding: em(80) 0;
    min-height: initial;
  }
  @media #{$mobL} {
    padding: em(50) 0;
    min-height: initial;
  }
}

.tabs {
  ul {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      list-style: none;
      margin: 0 em(30);
      a {
        font-size: em(65);
        font-family: $basefont;
        text-transform: uppercase;
        color: var(--gold);
        display: block;
        position: relative;
        &:before {
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          margin: auto;
          width: 0;
          height: 2px;
          background: var(--gold);
          @include transitions(0.5s);
          content: '';
        }
        &.active {
          &:before {
            width: 80%;
          }
        }

        &:hover {
          text-decoration: none;
        }
      }
    }
    @media #{$ipadL} and #{$ipadpro} {
      li {
        a {
          font-size: em(50);
        }
      }
    }
    @media #{$ipad}{
      li {
        a {
          font-size: em(50);
        }
      }
    }
    @media #{$mobL} {
      li {
        a {
          font-size: em(36);
        }
      }
    }
  }
}

.tabContent {
  display: none;
  padding: em(40) 0 0 0;
  &.active {
    display: block;
  }
}

.featuredSlider {
  min-height: em(645);
  .swiper-slide {
    height: em(645);
  }
  @media #{$ipadL} and #{$ipadpro} {
    min-height: em(400);
    .swiper-slide {
      height: em(400);
    }
  }
  @media #{$ipad}{
    min-height: em(400);
    .swiper-slide {
      height: em(400);
    }
  }
  @media #{$mobL} {
    min-height: em(400);
    .swiper-slide {
      height: em(400);
    }
  }
}

.featuredSliderControls {
  display: flex;
  justify-content: center;
  margin: em(30) 0 0 0;
}

.featuredSectionContent {
  @media #{$desktop} {
    opacity: 0;
    visibility: hidden;
    &.fadeIn {
      opacity: 1;
      visibility: visible;
      @include transitions(0.5s);
      .fadeInUp {
        opacity: 1;
        transform: translate(0, 0);
        visibility: visible;
      }
    }
  }
}

.fadeInUp {
  @media #{$desktop} {
    transform: translate(0, 20px);
    opacity: 0;
    visibility: hidden;
    @include transitions(1s);
  }
}

.cardAnimateWrapper {
  height: 100%;
  display: grid;
  grid-column-gap: em(30);
  grid-template-columns: repeat(3, 1fr);
  position: relative;
  z-index: 2;
  align-items: center;
}

.animateHeading {
  position: absolute;
  left: 0;
  top: em(150);
  z-index: 3;
  width: 100%;
  @include transitions(0.5s);
  h2, h6 {
    color: var(--navyBlue);
  }
}

.featuredAnimation {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  color: var(--navyBlue);
  @include transitions(1s);

  .container-s {
    height: 100%;
    padding: initial;
  }

  .animateItem {
    position: absolute;
    width: 32.2%;
    min-height: em(645);
    transform: translate(-50%, 0);
    left: 50%;
    @include transitions(1s);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    img {
      display: none
    }

    &:nth-child(2) {
      z-index: 1;
      width: 100%;
      height: 100%;
    }
  }

  &.animated {
    @extend .container-s;
    .animateHeading {
      opacity: 0;
      visibility: hidden;
    }
    .animateItem {
      &:nth-child(2) {
        z-index: 1;
        width: 32.2%;
        height: em(645);
      }
    }
    .cardAnimateWrapper {
      top: em(149);
    }
    &.animate-cards {
      .animateItem {
        &:first-of-type {
          left: 0;
          transform: translate(0%, 0);
        }

        &:last-of-type {
          left: initial;
          transform: translate(0%, 0);
          right: 0;
        }
      }
    }
  }

  &.fadeOut {
    opacity: 0;
    visibility: hidden;
  }

  @media #{$ipadpro}{
    display: none;
  }
}