.heroBanner {
  overflow: hidden;
  position: relative;

  .container-fluid,
  .container-s {
    position: relative;
    z-index: 2;
  }
  .videoBg {
    &:after {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0.85;
      background: linear-gradient(180deg, #000218 0%, #00133F 100%);
      content: '';
    }
  }
  @media #{$desktop} {
    &.animated {

      .heroSlider {
        @include scale(1);
        .slideContent, .hero-pagination {
          opacity: 1;
        }
      }
      .heroTopContent {
        top: em(180);
        right: 34%;
      }
      .heightSpace {
        height: em(450);
      }
      .heroLeftTopContent {
        top: 100%;
        opacity: 0;
        visibility: hidden;
      }
    }
  }
}

.heroTopContent {
  z-index: 3;
  display: flex;
  flex-direction: column;
  position: relative;
  padding: em(120) 2em em(50) 2em;
  @media #{$desktop} {
    @include transitions(1s);
    position: absolute;
    top: 50%;
    right: 0;
    padding: 0 em(100) 0 em(150);
  }
  @media #{$ipadpro} {
    padding: em(100) 37% em(50) 40%;
  }
  @media #{$ipad} {
    padding: em(100) 35% em(50) 34%;
  }
  @media #{$mobL} {
    align-items: center;
    padding: em(120) 2em em(50) 2em;
  }
}

.heroLeftTopContent {
  position: absolute;
  top: 0;
  left: em(100);
  z-index: 3;
  @include transitions(1s);
  padding: em(290) 0 0 0;
  display: flex;
  flex-direction: column;
  width: em(430);
  height: 80%;
  @media #{$ipadpro} {
    display: none;
  }

}

.leftHeading {
  font-size: em(100);
  line-height: 1;
  color: var(--gold);
  font-family: $basefont;
  text-align: end;
}

.btmTxt {
  margin-top: auto;
  color: var(--white);
  padding: 0 em(30) 0 0;
}

.heightSpace {
  height: em(60);
  width: 100%;
  @include transitions(0.5s);
  @media #{$ipadpro} {
    display: none;
  }
}

.heroSlider {
  height: 100vh;

  .swiper-slide {
    height: 100%;
    width: 100%;
    overflow: hidden;
    @media #{$desktop} {
      &:not(.swiper-slide-active) {
        .heroHeading {
          transform: translateY(40px);
          opacity: 0;
        }
        .heroTxt {
          transform: translateY(40px);
          opacity: 0;
        }
      }

      &-active {
        .heroHeading {
          transform: translateY(0);
          opacity: 1;
          transition-delay: 0.5s;
        }
        .heroTxt {
          transform: translateY(0);
          opacity: 1;
          transition-delay: 0.6s;
        }
      }
    }
  }

  @media #{$desktop} {
    @include scale(0.6);
    @include transitions(1s);
    .slideContent {
      opacity: 0;
      @include transitions(0.5s);
    }
  }
  @media #{$ipadpro} {
    height: 70vh;
  }
  @media #{$ipad} {
    height: 50vh;
  }
}

.slideBg {
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-color: var(--navyBlue);
  width: 100%;
  height: 100%;
  mix-blend-mode: multiply;
  &:before,
  &:after {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    content: '';
    z-index: 2;
  }
  &:before {
    background: linear-gradient(0deg, rgba(3, 7, 15, 0.00) 69.59%, #03070F 121.17%);
  }
  &:after {
    background: linear-gradient(0deg, rgba(3, 7, 15, 0.00) 58.01%, #03070F 109.59%);
  }
}

.slideVideo {
  position: relative;
  width: 100%;
  height: 100%;
  &:before,
  &:after {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
