    background: linear-gradient(to bottom, rgba(2, 7, 14, 0.58) 0%, rgba(2, 5, 14, 0.56) 1%, rgba(2, 6, 15, 0.54) 2%, rgba(2, 6, 15, 0.52) 3%, rgba(4, 8, 16, 0.5) 4%, rgba(2, 6, 14, 0.48) 5%, rgba(2, 7, 16, 0.44) 7%, rgba(2, 7, 16, 0.42) 8%, rgba(2, 5, 14, 0.4) 9%, rgba(3, 5, 16, 0.38) 10%, rgba(3, 5, 14, 0.36) 11%, rgba(0, 6, 14, 0.34) 12%, rgba(3, 9, 15, 0.32) 13%, rgba(0, 3, 13, 0.3) 14%, rgba(0, 7, 14, 0.28) 15%, rgba(4, 7, 15, 0.26) 16%, rgba(4, 8, 16, 0.24) 17%, rgba(4, 9, 13, 0.22) 18%, rgba(5, 9, 14, 0.2) 19%, rgba(5, 5, 16, 0.18) 20%, rgba(0, 6, 12, 0.16) 21%, rgba(0, 7, 13, 0.14) 22%, rgba(0, 7, 15, 0.12) 23%, rgba(0, 9, 9, 0.1) 24%, rgba(0, 0, 11, 0.08) 25%, rgba(13, 13, 13, 0.06) 26%, rgba(0, 18, 18, 0.05) 27%, rgba(0, 0, 0, 0.03) 28%, rgba(0, 0, 0, 0.02) 29%, rgba(255, 255, 255, 0) 30%, rgba(0, 0, 0, 0) 58%, rgba(0, 0, 0, 0.02) 59%, rgba(0, 0, 0, 0.04) 60%, rgba(0, 15, 15, 0.06) 61%, rgba(0, 0, 13, 0.08) 62%, rgba(9, 9, 19, 0.1) 63%, rgba(0, 8, 16, 0.12) 64%, rgba(7, 7, 14, 0.14) 65%, rgba(0, 6, 12, 0.16) 66%, rgba(0, 0, 11, 0.18) 67%, rgba(0, 5, 15, 0.2) 68%, rgba(4, 9, 13, 0.22) 69%, rgba(0, 4, 13, 0.24) 70%, rgba(0, 4, 12, 0.26) 71%, rgba(0, 4, 14, 0.28) 72%, rgba(3, 7, 17, 0.3) 73%, rgba(3, 6, 16, 0.32) 74%, rgba(3, 6, 15, 0.34) 75%, rgba(0, 6, 14, 0.36) 76%, rgba(5, 8, 16, 0.38) 77%, rgba(2, 5, 13, 0.4) 78%, rgba(2, 5, 15, 0.42) 79%, rgba(5, 9, 16, 0.44) 80%, rgba(4, 7, 16, 0.46) 81%, rgba(2, 6, 15, 0.48) 82%, rgba(2, 6, 14, 0.5) 83%, rgba(2, 6, 14, 0.52) 84%, rgba(4, 7, 15, 0.54) 85%, rgba(2, 5, 15, 0.56) 86%, rgba(3, 7, 16, 0.58) 87%, rgba(2, 5, 14, 0.6) 88%, rgba(3, 7, 15, 0.62) 89%, rgba(3, 8, 16, 0.64) 90%, rgba(3, 6, 14, 0.66) 91%, rgba(1, 6, 14, 0.68) 92%, rgba(4, 7, 16, 0.7) 93%, rgba(1, 6, 15, 0.72) 94%, rgba(4, 8, 16, 0.75) 96%, rgba(3, 5, 15, 0.77) 97%, rgba(3, 6, 14, 0.78) 98%, rgba(4, 7, 16, 0.8) 99%, rgba(2, 6, 15, 0.81) 100%);
  }
  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.slideContent {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: em(64) em(75);
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  z-index: 2;
  @media #{$ipad} {
    padding: 2em;
    display: block;
  }
  @media #{$mobL} {
    padding: 2em;
    display: block;
  }
}

.heroHeading {
  font-size: em(76);
  line-height: 1;
  color: var(--gold);
  font-family: $basefont;
  width: 50%;
  @media #{$desktop} {
    @include transitions(0.5s);
  }
  @media #{$ipadpro} {
    font-size: em(50);
  }
  @media #{$ipad} {
    width: 100%;
  }
  @media #{$mobL} {
    width: 100%;
    font-size: em(40);
  }
}

.heroTxt {
  width: 30%;
  @media #{$desktop} {
    @include transitions(0.5s);
  }
  @media #{$ipadpro} {
    width: 40%;
  }
  @media #{$ipad} {
    width: 100%;
  }
  @media #{$mobL} {
    width: 100%;
  }
}

.hero-pagination {
  position: absolute;
  left: initial !important;
  right: em(50);
  top: 0 !important;
  bottom: 0 !important;
  margin: auto;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 2;
  width: em(33) !important;

  .swiper {
    &-pagination-bullet {
      width: em(33);
      height: em(33);
      position: relative;
      background: none;
      opacity: 1;
      margin: 0 !important;
      display: block !important;
      &:before {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        background: var(--gold);
        width: em(11);
        height: em(11);
        @include borderRadius(50%);
        content: '';
      }
      &:after {
        position: absolute;
        left: 0;
        width: 100%;
        height: 100%;
        border: solid 1px var(--gold);
        @include borderRadius(50%);
        opacity: 0;
        @include transitions(0.5s);
        content: '';
      }
      &-active {
        &:after {
          opacity: 1;
        }
      }
    }
  }
  @media #{$desktop}{
    opacity: 0;
    @include transitions(0.5s);
  }
  @media #{$mobL} {
    right: em(20);
  }
}

.text1 {
  color: var(--gold);
  font-family: $basefont;
  font-size: em(50);
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  @media #{$ipadpro} {
    font-size: em(40);
  }
  @media #{$mobL} {
    font-size: em(30);
  }
}

.text2 {
  color: var(--gold);
  font-family: $basefont;
  font-size: em(100);
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  @media #{$ipadpro} {
    font-size: em(70);
  }
  @media #{$mobL} {
    font-size: em(70);
  }
}

.text3 {
  color: var(--gold);
  font-family: $basefont;
  font-size: em(50);
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  margin-left: auto;
  @media  #{$ipadpro} {
    font-size: em(40);
  }
  @media #{$mobL} {
    font-size: em(30);
    margin-left: initial;
  }
}