body {
  font-family: $arboriaBook;
  font-weight: 400;
  color: var(--white);
  line-height: 1;
  background: var(--black);
}

.loadjs {
  overflow: hidden;
}

.img-wrapper {
  position: relative;
}

.container-fluid {
  max-width: 100%;
  padding: 0;
}

.container-s {
  max-width: 100%;
  padding: 0 3.90625%;

  @media #{$ipad} {
    padding: 0 3em;
  }

  @media #{$mob} {
    padding: 0 2em;
  }
}

