.getInTouch {
  background: var(--navyBlue);
  padding: em(154) 0;
  position: relative;
  overflow: hidden;
  h6, h3 {
    color: var(--gold);
  }
  .videoBg {
    &:after {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0.85;
      background: linear-gradient(180deg, #000218 0%, #00133F 100%);
      content: '';
    }
  }
  .container-fluid,
  .container-s {
    position: relative;
    z-index: 2;
  }

  @media #{$ipadL} and #{$ipadpro} {
    padding: em(80) 0;
  }

  @media #{$ipad}{
    padding: em(80) 0;
  }
  @media #{$mobL} {
    padding: em(50) 0;
  }
}

.getInTouchForm {
  .form-group {
    margin: 0 0 0 0;
  }
}