
.text-center {
  text-align: center;
}

.m-auto {
  margin: 0 auto;
}

.m-0 {
  margin: 0 !important;
}

.mb-md {
  @media #{$ipad} {
    margin-bottom: 2em;
  }
}

.mb-sm {
  @media #{$mobL} {
    margin-bottom: 2em;
  }
}

.text-lower {
  text-transform: lowercase !important;
}

.text-upper {
  text-transform: uppercase !important;
}

.text-capitalize {
  text-transform: capitalize !important;
}

.generic--radius {
  border-radius: 10px;
}

.d-flex {
  display: flex;
  flex-wrap: wrap;
}

.justify-content-center {
  justify-content: center;
}

.justify-content-between {
  justify-content: space-between;
}

.justify-content-around {
  justify-content: space-around;
}

.justify-content-end {
  justify-content: flex-end;
}

.align-item-center {
  align-items: center;
}

.align-item-end {
  align-items: flex-end;
}

.w-100 {
  width: 100%;
}

.h-100 {
  height: 100%;
}

.mobile-only {
  @media #{$tab} {
    display: none;
  }
}

.desktop-only {
  @media #{$mob} {
    display: none;
  }
}

.img-hack {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.img-hack-contain {
  @extend .img-hack;
  object-fit: contain;
}

.mb-sm-0 {
  @media #{$mobL} {
    margin-bottom: 0 !important;
  }
}

// font weights
$fonts-w: (
        "200": 200,
        "300": 300,
        "400": 400,
        "500": 500,
        "600": 600,
        "700": 700,
        "800": 800,
);

@each $fontname, $fontvalue in $fonts-w {
  .fw-#{$fontname} {
    font-weight: $fontvalue;
  }
}




