.btn {
  @include borderRadius(30px);
  font-size: em(18);
  line-height: em(24);
  font-family: $basefont;
  padding: em(6) em(22) em(4) em(22);
  color: var(--white);
  border: solid 1px var(--white);
  background: none;
  @include transitions(0.5s);
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  min-height: em(44.5);
}

.btn-gold-border {
  color: var(--lightGold);
  border: solid 1px var(--lightGold);
}

.btn-navyBlue-border {
  color: var(--navyBlue);
  border: solid 1px var(--navyBlue);
}

@media (hover: hover) {
  .btn-gold-border {
    &:hover {
      background: var(--lightGold);
      color: var(--navyBlue);
      border-color: var(--lightGold);
    }
  }
  .btn-navyBlue-border {
    &:hover {
      background: var(--navyBlue);
      color: var(--gold);
      border-color: var(--navyBlue);
    }
  }
}