
:root {
  --white: #FFFFFF;
  --black: #000000;
  --navyBlueDark: #000014;
  --navyBlue: #001340;
  --blue: #00206B;
  --gold: #C9B79F;
  --offWhite: #E4D7C8;
  --borderColor: #e5d8c740; //#E5D8C7
  --lightGold: #E5D8C7;
}

// NOTE: above colors will be used with rgba() function

// NOTE:  sample sass variable : i will not use this now.
// $primarycolor: var(--primary-color);

// font families
$basefont: 'Oriya MN';
$secondaryFont: 'Arboria';
$arboriaBook: 'Arboria-Book';

$transition: all 0.5s ease;
$transition-linear: all 0.5s linear;
$transition-bounce: all 0.3s cubic-bezier(.47, 1.64, .41, .8);

// Media Sizes
// Mobile first approach
$tab: '(min-width:576px)';
$tabL: '(min-width:768px)';
$ipadL: '(min-width:992px)';
$ipadXL: '(min-width:1025px)';
$desktop: '(min-width:1200px)';

// Desktop first approach
$ipadpro: '(max-width:1199.98px)';
$ipad: '(max-width:991.98px)';
$mobL: '(max-width:767.98px)';
$mob: '(max-width:575.98px)';