.newsAndMedia {
  padding: em(150) 0;
  position: relative;
  overflow: hidden;
  background: var(--navyBlueDark);
  h3, h6 {
    color: var(--gold);
  }
  h3 {
    margin-bottom: em(30);
  }
  .videoBg {
    background: var(--navyBlueDark);

    &:after {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0.5;
      background: linear-gradient(180deg, #00133F 0%, #000218 100%);
      content: '';
    }
    &:before {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0.5;
      background: linear-gradient(180deg, #000218 0%, #00133F 100%);
      content: '';
    }
    video {
      mix-blend-mode: color-dodge;
    }
  }
  .container-fluid,
  .container-s {
    position: relative;
    z-index: 2;
  }
  @media #{$ipadL} and #{$ipadpro} {
    padding: em(80) 0;
  }
  @media #{$ipad}{
    padding: em(80) 0;
  }
  @media #{$mobL} {
    padding: em(50) 0;
  }
}

.videoBgOverly {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(102.15% 61.85% at 26.18% 61.99%, rgba(0, 19, 64, 0.00) 0%, rgba(1, 15, 48, 0.90) 100%);
}

.newsContent {
  padding: 0 0 0 12%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: em(680);
  @media #{$ipad}{
    min-height: em(300);
    padding: 0 3em 2em 3em;
  }
  @media #{$mobL} {
    padding: 0 2em 2em 2em;
    min-height: initial;
  }
}

.newsText{
  @media #{$mobL} {
    margin-bottom: em(30);
  }
}

.newsSlider {
  overflow: hidden;
  min-height: em(680);
  padding: 0 3.90625% 0 0;
  .swiper-slide {
    height: auto;
    width: em(480);
  }
  @media #{$ipadL}{
    min-height: em(580);
  }
  @media #{$ipad} {
    min-height: em(580);
    padding: 0 3em 0 3em;
  }
  @media #{$mobL} {
    padding: 0 2em;
    min-height: em(500);
  }
}

.newsSliderControls {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
}