#header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  padding: em(40) 0 0 0;
  z-index: 9;
  @include transitions(0.5s);
  &:before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 0;
    background: var(--navyBlue);
    @include transitions(0.5s);
    content: '';
  }
  .container-s,
  .container-fluid {
    position: relative;
    z-index: 2;
  }
  &.sticky {
    padding: em(20) 0 em(20) 0;
    font-size: em(14);
    &:before {
      height: 100%;
    }
  }

  @media #{$ipadpro}{
    padding: em(20) 0 0 0;
  }
}

.logo {
  width: em(260);
  height: em(71);
  display: block;
  @media #{$ipadpro} {
    width: em(180);
    height: em(50);
  }
}

.topNavigation {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 0 0 em(17);
  ul {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    li {
      list-style: none;
      a {
        font-family: $basefont;
      }
    }
  }
  @media #{$ipadpro} {
    position: fixed;
    top: 0;
    right: -30%;
    width: 30%;
    height: 100%;
    background: var(--navyBlue);
    padding: em(100) em(30) em(30) em(30);
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    z-index: 999;
    @include transitions(0.5s);
    ul {
      display: block;
      li {
        display: block;
      }
    }
    &.active{
      right: 0;
    }
  }

  @media #{$ipad} {
    width: 45%;
    right: -45%;
  }

  @media #{$mobL} {
    width: 100%;
    right: -100%;
    &.active{
      right: 0;
    }
  }
}

.mainNav {
  ul {
    li {
      margin: 0 em(47) 0 0;
      &:last-child {
        margin: 0;
      }
      a {
        font-size: em(18);
        @include transitions(0.5s);
        display: inline;
        padding-bottom: 5px;
        background: linear-gradient(to right, var(--gold) 0%, var(--gold) 98%);
        background-size: 0px 1px;
        background-repeat: no-repeat;
        background-position: left 85%;
        &:hover {
          text-decoration: none;
        }
        &.active {
          background-size: 100% 1px;
          text-decoration: none;
          color: var(--gold);
        }
      }
    }
  }
  @media #{$ipadpro} {
    width: 100%;
    ul {
      li {
        margin: 0;
        a {
          display: block;
          color: var(--white);
          padding: em(20) 0;
          border-bottom: solid 1px var(--white);
        }
        &:last-child{
          a{
            border-bottom: 0;
          }
        }
      }
    }
  }
}

.topSearch {
  margin-left: auto;
  ul {
    li {
      margin: 0 em(13) 0 0;
      &:last-child {
        margin: 0;
      }
      .btn {
        padding: em(11) em(22) em(7) em(22);
        display: block;
        text-align: center;
      }
    }
  }
  @media #{$ipadpro} {
    margin-top: auto;
    width: 100%;
    margin-left: initial;
    ul {
      li {
        margin: 0;
      }
    }
  }
}

.searchBtn {
  width: em(32);
  height: em(32);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  @include borderRadius(50%);
  &:before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: var(--white);
    opacity: 0.2;
    @include transitions(0.5s);
    content: '';
  }
  i {
    width: em(12);
    height: em(12);
    position: relative;
    z-index: 1;
  }
  @media #{$ipadpro} {
    display: none;
  }
}

.mobileNav {
  ul {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    li {
      list-style: none;
      width: em(50);
      height: em(50);
      margin: 0 0 0 em(15);
      &:first-child {
        margin: 0;
      }
      a, button {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: center;
        align-items: center;
        border: none;
        @include borderRadius(50%);
        background: rgba(#FFFFFF, 0.2);
        padding: em(10);
        span {
          display: block;
          width: 100%;
          height: 2px;
          background: var(--gold);
          position: relative;
          &:after, &:before {
            position: absolute;
            top: 0;
            left: 0;
            margin: auto;
            background: var(--gold);
            width: 100%;
            height: 2px;
            content: '';
          }
          &:before {
            top: em(-8);
          }
          &:after {
            top: em(7);
          }
        }
        i {
          width: em(20);
          position: relative;
          z-index: 1;
          img {
            width: 100%;
          }
        }
      }
    }
  }
  @media #{$desktop} {
    display: none;
  }
}

.menuClose {
  position: absolute;
  right: em(30);
  top: em(30);
  width: em(50);
  height: em(50);
  background: var(--gold);
  @include borderRadius(50%);
  border: none;
  i {
    width: em(30);
    height: 3px;
    background: var(--navyBlue);
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: 0;
    bottom: 0;
    &:first-of-type {
      transform: rotate(45deg);
    }
    &:last-of-type {
      transform: rotate(-45deg);
    }
  }
  @media #{$desktop} {
    display: none;
  }
}

@media (hover: hover) {
  .mainNav {
    ul {
      li {
        a {
          &:hover {
            background-size: 100% 1px;
            text-decoration: none;
            color: var(--gold);
          }
        }
      }
    }
  }
  .searchBtn {
    &:hover {
      &:before {
        background: var(--gold);
      }
    }
  }
}