h1,
.h1 {
  font-size: em(50);
  font-weight: 400;
  line-height: 1em;
  margin: 0 0 0.5em 0;
  font-family: $basefont;

  @media #{$ipadpro} {
    font-size: 3.5em;
  }

  @media #{$mob} {
    font-size: 2em;
    margin-bottom: 0.345em;
  }
}

h2,
.h2 {
  font-size: em(76);
  font-weight: 400;
  line-height: 1;
  margin: 0 0 0.500em 0;
  font-family: $basefont;

  &.font-58 {
    @media #{$desktop} {
      font-size: 3.625em;
    }
  }

  @media #{$ipadpro} {
    font-size: 2.5em;
  }

  @media #{$ipad} {
    font-size: 2em;
    margin-bottom: 0.545em;
  }
}

h3,
.h3 {
  font-size: em(60);
  font-weight: 400;
  line-height: 1em;
  margin: 0 0 0.5em 0;
  font-family: $basefont;

  @media #{$ipadpro} {
    font-size: 1.75em;
  }
}

h4,
.h4 {
  font-size: 2.125em;
  font-weight: 400;
  line-height: 1em;
  margin: 0 0 0.5em 0;
  font-family: $basefont;

  @media #{$ipadpro} {
    font-size: 1.25em;
  }
}

h5,
.h5 {
  font-size: em(40);
  font-weight: 400;
  line-height: 1em;
  margin: 0 0 em(20) 0;
  font-family: $basefont;
  @media #{$ipadL}{
    font-size: em(28);
  }
}

h6,
.h6 {
  font-size: em(20);
  font-weight: 400;
  line-height: 1em;
  margin: 0 0 em(33) 0;
  font-family: $basefont;
  @media #{$mobL}{
    margin-bottom: em(20);
  }
}

p,
.p {
  font-size: em(16);
  line-height: 1.3;
  letter-spacing: em(1);
  margin: 0 0 em(54) 0;
  font-weight: 300;
  @media #{$ipadpro} {
    font-size: 1em;
    margin-bottom: 1em;
  }
  &.lead {
    font-size: em(22);

  }
  &.medium {
    font-size: em(18);
    margin-bottom: em(30);
  }
}

.smallHeading {
  letter-spacing: em(6);
  font-family: $arboriaBook;
  font-size: em(18);
  margin: 0 0 em(20) 0;
}