.swiper-button-next {
  width: em(42);
  height: em(42);
  @include borderRadius(16px);
  position: relative;
  top: initial;
  bottom: initial;
  left: initial;
  right: initial;
  margin: 0 0 0 em(10);
  overflow: hidden;
  &:before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: var(--white);
    opacity: 0.1;
    content: '';
  }
  i {
    width: em(14);
    height: em(12);
    img {
      width: 100%;
      height: 100%;
    }
  }
  &:after {
    display: none;
  }
  @media #{$mobL} {
    @include borderRadius(8px);
  }
}

.swiper-button-prev {
  width: em(42);
  height: em(42);
  @include borderRadius(16px);
  position: relative;
  top: initial;
  bottom: initial;
  left: initial;
  right: initial;
  margin: 0 10px 0 0;
  overflow: hidden;
  &:before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: var(--white);
    opacity: 0.1;
    content: '';
  }
  i {
    width: em(14);
    height: em(12);
    img {
      width: 100%;
      height: 100%;
    }
  }
  &:after {
    display: none;
  }
  @media #{$mobL} {
    @include borderRadius(8px);
  }
}

.swiper-pagination {
  position: initial !important;
  top: initial;
  bottom: initial;
  left: initial;
  right: initial;
  width: auto !important;
  display: flex;
  flex-direction: row;
  align-items: center;
  .swiper-pagination-bullet {
    width: em(6);
    height: em(6);
    @include transitions(0.5s);
    @include borderRadius(25px);
    background: var(--lightGold);
    opacity: 0.5;
    margin: 0 em(5) 0 0;
    &:last-child {
      margin: 0;
    }
    &.swiper-pagination-bullet-active {
      width: em(16);
      opacity: 1;
    }
  }
}