.form-wrapper {
  width: 100%;
  position: relative;
}

.form-group-col {
  display: flex;
  justify-content: space-between;
  .form-group {
    width: 48%;
  }
  @media #{$mobL}{
    display: block;
    .form-group {
      width: 100%;
    }
  }
}

.form-group {
  margin-bottom: em(40);
}

.labelHide {
  margin: 0;
  padding: 0;
  font-size: 0;
  text-indent: -99999px;
  height: 0;
  display: block;
}

.form-control {
  border: none;
  border-bottom: solid 1px var(--borderColor);
  background: none;
  font-size: em(16);
  width: 100%;
  height: em(53);
  padding: em(17) 0 em(17) 0;
  font-weight: 300;
  color: var(--gold);
  font-family: $arboriaBook;
  letter-spacing: 1px;
  &.textArea {
    height: em(130);
    resize: none;
    overflow: hidden;
  }

  &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: var(--white);
  }
  &::-moz-placeholder { /* Firefox 19+ */
    color: var(--white);
  }
  &:-ms-input-placeholder { /* IE 10+ */
    color: var(--white);
  }
  &:-moz-placeholder { /* Firefox 18- */
    color: var(--white);
  }

  &:-webkit-autofill,
  &:-webkit-autofill:hover,
  &:-webkit-autofill:focus,
  &:-webkit-autofill:active {
    color: var(--white);
    -webkit-text-fill-color: var(--white);
    box-shadow: none;
    background: none;
  }
}

.checkBox {
  display: flex;
  align-items: flex-start;
  + .checkBox {
    margin-top: em(16);
  }
}

input[type="checkbox"] {
  /* Add if not using autoprefixer */
  -webkit-appearance: none;
  /* Remove most all native input styles */
  appearance: none;
  /* For iOS < 15 */
  background-color: var(--lightGold);
  /* Not removed via appearance */
  margin: 0 em(14) 0 0;
  font: inherit;
  color: var(--white);
  width: em(18);
  height: em(18);
  border: 1px solid var(--lightGold);
  border-radius: 4px;
  transform: translateY(-0.075em);
  display: grid;
  place-content: center;
  flex-shrink: 0;
  position: relative;
  top: 2px;
}

input[type="checkbox"]::before {
  content: "";
  width: em(10);
  height: em(10);
  clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
  transform: scale(0);
  transform-origin: bottom left;
  transition: 120ms transform ease-in-out;
  box-shadow: inset 1em 1em var(--navyBlue);
  /* Windows High Contrast Mode */
  background-color: var(--navyBlue);
}

input[type="checkbox"]:checked::before {
  transform: scale(1);
}

input[type="checkbox"]:focus {
  outline: max(2px, 0.15em) solid var(--lightGold);
  outline-offset: max(2px, 0.15em);
}

input[type="checkbox"]:disabled {
  color: var(--navyBlue);
  cursor: not-allowed;
}

.intelInput {
  .iti {
    width: 100%;
    &__dropdown-content {
      margin: 0;
      border: none;
      box-shadow: none;
    }
    &__arrow {
      position: absolute;
      background: url("../svg/down-arrow.svg") center/contain no-repeat;
      width: em(12);
      height: em(8);
      top: 45%;
      right: em(20);
      transition: 0.2s ease;
      border: none;
      margin: 0;
    }
    &__country-container {
      padding: 0;
      width: em(116);
    }
    &__selected-country {
      width: 100%;
    }
    &__tel-input {
      background: none;
      border: none;
      border-bottom: solid 1px var(--borderColor);
      width: 100%;
      padding: em(14) 0 em(17) em(136) !important;
      height: em(53);
      color: var(--gold);
      font-family: $arboriaBook;
      letter-spacing: 1px;
      font-size: em(16);
      &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: var(--white);
      }
      &::-moz-placeholder { /* Firefox 19+ */
        color: var(--white);
      }
      &:-ms-input-placeholder { /* IE 10+ */
        color: var(--white);
      }
      &:-moz-placeholder { /* Firefox 18- */
        color: var(--white);
      }
    }
    &__selected-country {
      &:after {
        position: absolute;
        right: 0;
        top: 0;
        height: 80%;
        width: 1px;
        background: var(--borderColor);
        content: '';
      }
    }
    &__country {
      background: var(--white);
      color: var(--navyBlueDark);
      margin: 0;
      padding: 10px;
      box-sizing: border-box;
      cursor: pointer;
      font-weight: 300;
    }
    &__highlight {
      background-color: var(--gold);
      color: var(--white);
    }
    &__dial-code {
      color: var(--navyBlueDark);
    }
  }
}