/* the container must be positioned relative:*/
.custom-select {
  position: relative;
  text-align: left;
  z-index: 2;
}

.custom-select select {
  display: none; /*hide original SELECT element:*/
}

/*style the arrow inside the select element:*/
.select-selected:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 1em;
  transition: 0.2s ease;
  margin-top: 0;
  padding: 0;
  background: url("../svg/down-arrow.svg") center/contain no-repeat;
  width: em(12);
  height: em(8);
}

/*point the arrow upwards when the select box is open (active):*/
.select-selected.select-arrow-active:after {
  transform: scale(-1);
}

// .select-items div,
.select-selected {
  user-select: none;
  width: 100%;
  height: 100%;
  cursor: pointer;
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  background-color: transparent;
  font-size: em(16);
  font-weight: 400;
  line-height: 1.2em;
  padding: em(17) 0 0 0;
  border-radius: 0;
  border-bottom: 1px solid var(--borderColor);
  color: var(--white);
  @media #{$mob} {
    font-size: 1em;
    padding: 0.9em 0.5em 0.9em;
  }
}

/*style items (options):*/
.select-items {
  position: absolute;
  width: 100%;
  overflow: hidden;
  border-top: 1px solid #eee;
  z-index: 1;
  background: var(--white);
  transition: all 0.3s ease-in-out;
  box-shadow: 0 4px 17px rgba(55, 72, 106, 0.1);
  border-radius: 0;
  top: 3.5em;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  max-height: 10em;
  overflow-y: auto;
  color: var(--navyBlueDark);

  @media #{$ipadpro} {
    // top: 4em;
  }

  > div {
    padding: 10px;
    box-sizing: border-box;
    cursor: pointer;
    font-weight: 300;
  }
}

/*hide the items when the select box is closed:*/
.select-hide {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  top: 4em;
  // @media #{$ipadpro} {
  //   // top: 5em;
  // }
}

.select-items div:hover,
.same-as-selected {
  background-color: var(--gold);
  color: var(--white);
}
