.newsCard {
  border: solid 1px var(--borderColor);
  overflow: hidden;
  position: relative;
  @include transitions(0.5s);
  color: var(--gold);
  height: 100%;
  display: flex;
  flex-direction: column;
  &:before {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0;
    @include transitions(0.8s);
    background: var(--gold);
    content: '';
  }
  &.darkCard{
    border-color: rgba(#000014, 0.4);
    color: var(--navyBlueDark);
    &:before{
      background: var(--navyBlueDark);
    }
  }
}

.newCardText {
  padding: em(49) em(50);
  position: relative;
  z-index: 1;
  min-height: em(400);
  display: flex;
  flex-direction: column;
  .head {
    display: flex;
    justify-content: space-between;
    margin: 0 0 em(59) 0;
    p {
      letter-spacing: 5px;
    }
    @media #{$ipad} {
      margin: 0 0 em(30) 0;
    }
  }
  .content {
    margin: 0 0 em(60) 0;
    p {
      letter-spacing: initial;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
    }
    @media #{$ipadL} and #{$ipadpro} {
      margin: 0 0 em(40) 0;
    }
    @media #{$ipad} {
      margin: 0 0 em(30) 0;
    }
  }
  @media #{$ipad} {
    padding: em(30);
    min-height: em(300);
  }
}

.newsDate {
  margin-top: auto;
}

.newsCardImg {
  width: 100%;
  height: em(280);
  margin-top: auto;
  img {
    width: 100%;
    height: 100%;
    @include scale(1);
    @include transitions(0.8s);
  }

  @media #{$mobL}{
    height: em(200);
  }
}

.fullLink {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  z-index: 3;
}

.featuredCard {
  position: relative;
  overflow: hidden;
  height: 100%;
}

.fImg {
  position: relative;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  &:before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 58.53%, #000 119.59%);
    z-index: 1;
    content: '';
  }
  img {
    @include scale(1);
    @include transitions(0.8s);
    transform: translate3d(0, 0, 0);
  }
}

.fContent {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 2;
  width: 100%;
  height: auto;
  padding: em(18) em(20);
}

.fTxt {
  color: var(--white);
  font-size: em(28);
  line-height: 1;
  @media #{$mobL} {
    font-size: em(22);
  }
}

@media (hover: hover) {
  .newsCard {
    &:hover {
      border-color: var(--gold);
      color: var(--blue);
      &:before {
        height: 100%;
      }
      .newsCardImg {
        img {
          @include scale(1.05);
        }
      }
    }
    &.darkCard{
      &:hover{
        border-color: var(--navyBlueDark);
        color: var(--lightGold);
      }
    }
  }
  .featuredCard {
    &:hover {
      img {
        @include scale(1.05);
      }
    }
  }
}